import React from "react";
import s from "./Filters.module.scss";

function Filters({ onSelectFilterPriceFrom, onSelectFilterPriceTo, onSelectFilterSquareFrom, onSelectFilterSquareTo }) {
	return (
		<div className={s.filters__inner}>
			<div className={s.filters__item}>
				<p className={s.filter}></p>
				<div className={s.input}>
					<div>
						<input type="text" onChange={onSelectFilterPriceFrom} placeholder="" />
						--
						<input type="text" onChange={onSelectFilterPriceTo} placeholder="" />
					</div>
				</div>
			</div>
			<div className={s.filters__item}>
				<p className={s.filter}></p>
				<div className={s.input}>
					<div>
						<input type="text" onChange={onSelectFilterSquareFrom} placeholder="" />
						--
						<input type="text" onChange={onSelectFilterSquareTo} placeholder="" />
					</div>
				</div>
			</div>
		</div>
	)
}

export default Filters;